import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
    routes: [{
        path: '/',
        component: () =>
                import('../pages/Welcome')
    }, {
        path: '/employee',
        component: () =>
                import('../pages/Employee')
    }, {
        path: '/car-dealer',
        component: () =>
                import('../pages/CarDealer')
    }, {
        path: '/user',
        component: () =>
                import('../pages/User')
    }, {
        path: '/second-hand-car',
        component: () =>
                import('../pages/SecondHandCar')
    }, {
        path: '/second-hand-car/add',
        component: () =>
                import('../pages/SecondHandCarEditor')
    },
    {
        path: '/second-hand-car/edit/:id',
        component: () =>
                import('../pages/SecondHandCarEditor')
    },
    {
        path: '/rent',
        component: () =>
                import('../pages/RentCar')
    },
    {
        path: '/finance',
        component: () =>
                import('../pages/Finance')
    },
    {
        path: '/insurance',
        component: () =>
                import('../pages/Insurance')
    }
    ]
});



